.game-name {
    background-color: #00adff;
    /* background-image: url(../../../public/assets/images/bright-bubbles_header.jpg); */
    background-image: url(/once-and-done/static/media/bright-bubbles_header.b8df25b7.jpg);
    background-size: cover;
    color: black;
    text-shadow: 3px 3px 0px rgba(63,107,169, 0.5);
