.sub-header-background {
    background-color: #00adff;
    /* background-image: url(../../images/bright-bubbles_smaller.jpg);
    background-size: cover; */
}

.sub-header-static-text {
    line-height: 75px;
    font-size: 30px;
    text-align: center;
    font-weight: bold;
    margin-top: 0;
    margin-bottom: 0;
}

.sub-header-game-status {
    text-align: center;
    font-size: 20px;
    padding-bottom: 20px;
    font-family: cursive;
}

.status-message {
    display: block;
    line-height: 20px;
    font-style: italic;
    font-size: 20px;
    padding-left: 50px;
    margin-top: 0;
    margin-bottom: 15px;
    text-align: center;
    color: white;

}

.score, .top-score {
    display: inline;
    text-align: right;
    font-weight: bold;
    font-size: 25px;
    padding-left: 50px;
    position: relative;

}

.subfooter-background {
    /* background-color: #32a263; */
    background-image: url(/once-and-done/static/media/bright-bubbles_subfooter.482ac460.jpg);
    background-size: cover;
    display: inline-block;
    height: 150px;
    width: 100%;
}

.subfooter-text {
    color: transparent;
}


